/* Custom dropdown */
.vs-dropdown {
  position: relative;
  display: inline;
  vertical-align: middle;
  width: 100%; }

.vs-dropdown select {
  height: 40px;
  width: 100%;
  padding: 8px;
  padding-right: 40px;
  margin: 0;
  text-overflow: ellipsis;
  -webkit-appearance: none;
  /* hide default arrow in chrome OSX */
  box-shadow: 1px 0 1px 0 rgba(118, 121, 123, 0.16);
  border-radius: 2px;
  font-family: "Roboto", Arial, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-stretch: normal;
  font-weight: 400;
  line-height: 1.71;
  letter-spacing: normal;
  margin: 0;
  text-transform: capitalize;
  border-style: solid;
  border-width: 1px;
  background-color: #FFFFFF;
  color: #00263A;
  border-color: #EEEEEE; }

.vs-dropdown select::-ms-expand {
  display: none; }

.vs-dropdown::before,
.vs-dropdown::after {
  content: "";
  position: absolute;
  pointer-events: none; }

.vs-dropdown::after {
  /*  Custom dropdown arrow */
  content: "\25BE";
  width: 16px;
  height: 16px;
  font-size: 14px;
  line-height: 1;
  right: 16px;
  top: 50%;
  margin-top: -.5em;
  text-align: center; }

.vs-dropdown::before {
  /*  Custom dropdown arrow cover */
  right: 0;
  top: 0;
  bottom: 0;
  border-radius: 0 2px 2px 0; }

.vs-dropdown select[disabled] {
  max-width: none;
  text-overflow: none;
  background-color: #FAFAFA;
  color: #BBBCBD;
  border-color: #BBBCBD; }

.vs-dropdown select[disabled]::after {
  color: #BBBCBD; }

.vs-dropdown::before {
  background-color: #FFFFFF; }

.vs-dropdown::after {
  color: #54585A; }

.vs-dropdown select:focus {
  border-color: #069ABC;
  box-shadow: 0 0 2px 0 #069ABC; }

