/* Checkbox */
/* The checkbox container */
.vs-checkbox {
  display: flex;
  align-items: center;
  position: relative;
  padding-left: 32px;
  cursor: pointer;
  font-family: "Roboto", Arial, sans-serif;
  font-size: 14px;
