  font-weight: 400;
  line-height: 1.71;
  letter-spacing: normal;
  color: #00263A;
  margin: 0;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  height: 24px; }

/* Hide the browser's default checkbox */
.vs-checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0; }

/* Create a custom checkbox */
.vs-checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 24px;
  width: 24px;
  box-shadow: inset 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  border-radius: 2px;
  border-style: solid;
  border-width: 1px;
  background-color: #FFFFFF;
  color: #00263A;
  border-color: #EEEEEE; }

/* On mouse-over, add a grey background color */
.vs-checkbox:hover input ~ .vs-checkmark {
  border-color: #069ABC;
  box-shadow: 0 0 2px 0 #069ABC;
  background-color: #FFFFFF; }

/* When the checkbox is checked, add a blue background */
.vs-checkbox input:checked ~ .vs-checkmark {
  width: 24px;
  height: 24px;
  box-shadow: inset 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  border-radius: 2px;
  border-style: solid;
  border-width: 1px;
  background-color: #FFFFFF;
  border-color: #EEEEEE; }

.vs-checkbox input:disabled ~ .vs-checkmark {
  width: 24px;
  height: 24px;
  box-shadow: inset 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  border-radius: 2px;
  border-style: solid;
  border-width: 1px;
  background-color: #FAFAFA;
  border-color: #BBBCBD; }

/* Create the checkmark/indicator (hidden when not checked) */
.vs-checkmark:after {
  content: "";
  position: absolute;
  display: none; }

/* Show the checkmark when checked */
.vs-checkbox input:checked ~ .vs-checkmark:after {
  display: block; }

/* Style the checkmark/indicator */
.vs-checkbox .vs-checkmark:after {
  left: 8px;
  top: 2px;
  width: 8px;
  height: 16px;
  border: solid #006BA6;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg); }

